.userTable{
    width: 60%;
    margin: 50px auto;
    background-color: white;
    padding: 50px;
    border-radius: 10px;
    box-shadow: 1px 1px 8px 1px rgba(0, 0, 0, 0.065);
   
}

.userTable table{
    width: 100%;
    margin-top: 30px;

}

.userTable table thead th{
    background-color: darkcyan;
    border: 1px solid #dee2e6;
    color: white;
}

.userTable table tbody td{
    padding: 10px;
    text-align: center;
    vertical-align: middle;
}

.actionButtons button{
    margin: 0 10px;
}